@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter", sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.app--card {
  display: flex;
  flex-direction: column;
  gap: 2em;
}

hr {
  align-self: center;
  border-top: 3px solid rgba(187, 187, 187, 0.103);
  border-bottom: none;
  border-left: none;
  border-right: none;
  width: 45em;
}
